<template>
  <el-drawer
    :model-value="visible"
    title="高级筛选"
    size="550"
    class="custom-drawer"
    :before-close="handleClose"
  >
    <div class="body-container">
      <el-form label-position="top">
        <el-form-item label="企业名称">
          <el-input
            v-model="formData.companyName"
            placeholder="请输入企业名称"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="成交状态">
          <el-select v-model="formData.dealStatus" placeholder="全部" clearable>
            <el-option
              v-for="item in dealStatusList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="服务状态">
          <el-select
            v-model="formData.serviceStatus"
            placeholder="全部"
            clearable
          >
            <el-option
              v-for="item in serviceStatusList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="创建人名称">
          <el-input
            v-model="formData.createdByName"
            placeholder="请输入创建人名称"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="创建人账号">
          <el-input
            v-model="formData.createdByAccount"
            placeholder="请输入创建人账号"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="客户来源">
          <el-select
            v-model="formData.customerResource"
            placeholder="全部"
            clearable
          >
            <el-option
              v-for="item in customerResourceList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </template>
  </el-drawer>
</template>

<script setup lang="ts">
import { shallowReactive } from "vue";
import { useModel } from "@assets/useHook";

const props = defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
  (e: "emitFunc", value: Record<string, any>);
}>();
const visibleModel = useModel(props, "visible", emits);
const formData = shallowReactive({
  companyName: "",
  dealStatus: "",
  serviceStatus: "",
  createdByName: "",
  createdByAccount: "",
  customerResource: "",
});
// 客户来源
const customerResourceList = [
  { label: "邀请客户", value: 0 },
  { label: "搜索添加", value: 1 },
  { label: "接口客户", value: 3 },
];
// 服务状态
const serviceStatusList = [
  { label: "审批中", value: 0 },
  { label: "服务中", value: 1 },
  { label: "已驳回", value: 2 },
];
// 成交状态
const dealStatusList = [
  { label: "未成交", value: 0 },
  { label: "已成交", value: 1 },
];
const handleClose = () => {
  visibleModel.value = false;
};
const handleSubmit = () => {
  emits("emitFunc", formData);
  handleClose();
};
</script>

<style scoped lang="scss">
.el-select {
  width: 100%;
}
</style>
